@import '../../assets/scss/com.scss';
.location{
    width: 100%;
    padding: 0px 30px;
    overflow-y: scroll;
    box-sizing: border-box;
    .location_city{
        border: 2px solid $myBackColor;
        border-radius: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px 0;
        margin: 10px 0px;
        font-size: 35px;
        .city{
            color:$myBackColor ;
        }
        img{
            width: 30px;
            height: 38px;
            margin-right: 10px;
        }
    }
    .location_item{
        width: 100%;
        height: 100%;
        .item_initials{
            padding: 12px 0;
            box-sizing: border-box;
            font-size: 36px;
            font-family: Microsoft YaHei;
            font-weight: 400;
            color: #363636;
            .initials_city{
                margin: 20px 0px;
            }
        }
    }
    .initials_fiexed{
        position: fixed;
        top: 15%;
        bottom: 0;
        right: 2%;
        width: 30px;
        z-index: 999;
        .fiexed_box{
            width: 40px;
            height: 40px;
            border-radius: 50%;
            display: inline-block;
            font-size: 10px;
            margin: 10px 0;
            font-weight: 500;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .fiexed_boxcolor{
            width: 40px;
            height: 40px;
            border-radius: 50%;
            display: inline-block;
            font-size: 10px;
            margin: 10px 0;
            font-weight: 500;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: $myBackColor;
            color: #fff;
        }
    }
}